import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'head',
      component: () => import('@/components/header'),
      redirect: '/',
      children: [{
        name: 'dash',
        path: '/',
        component: () => import('@/views/dashboard')
      },
      {
        name: 'gameList',
        path: '/gameList',
        component: () => import('@/views/gameList')
      },
      {
        name: 'topList',
        path: '/topList',
        component: () => import('@/views/topList')
      },
      {
        path: '/detail',
        name: 'detailView',
        component: () => import('@/views/detailView')
      },
      {
        path: '/search',
        name: 'search',
        component: () => import('@/views/searchView')
      },
      {
        path: '/aboutus',
        name: 'aboutus',
        component: () => import('@/views/aboutus')
      },
      {
        path: '/terms',
        name: 'terms',
        component: () => import('@/views/terms')
      }, {
        path: '/appDown',
        name: 'appDown',
        component: () => import('@/views/appDown')
      }, {
        path: '/privacy',
        name: 'privacy',
        component: () => import('@/views/privacy')
      }, {
        path: '/diwu',
        name: 'diwu',
        component: () => import('@/views/details/diwu')
      }, {
        path: '/mingri',
        name: 'mingri',
        component: () => import('@/views/details/mingri')
      }, {
        path: '/mingri1',
        name: 'mingri1',
        component: () => import('@/views/details/mingri1')
      }, {
        path: '/mogu',
        name: 'mogu',
        component: () => import('@/views/details/mogu')
      }, {
        path: '/mogu1',
        name: 'mogu1',
        component: () => import('@/views/details/mogu1')
      }]
    }, {
      path: '*',
      name: '404',
      component: () => import('@/views/404')
    }
  ]
})



